"use client";

import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
} from "~/components/ui/card";
import { Button } from "~/components/ui/button";
import { ScrollArea } from "~/components/ui/scroll-area";
import { Badge } from "~/components/ui/badge";
import { useEffect, useState } from "react";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "~/components/ui/alert-dialog";
import { toast } from "sonner";
import { useAtom } from "jotai";
import {
  selectedSummaryAtom,
  summaryFromSavedAtom,
} from "~/states/summaryAtom";

// Interface for summary data structure
interface Summary {
  id: string;
  text: string;
  summary: string;
  createdAt: string;
}

interface ProcessedSummary {
  id: string;
  title: string;
  content: string;
  date: string;
  originalText: string;
  tags: string[];
}

// Convert to client component to better control fetching
export function SavedSummariesList() {
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(false);
  const [processedSummaries, setProcessedSummaries] = useState<
    ProcessedSummary[]
  >([]);
  const [summaryToDelete, setSummaryToDelete] = useState<string | null>(null);
  const [, setSelectedSummary] = useAtom(selectedSummaryAtom);
  const [, setSummaryFromSaved] = useAtom(summaryFromSavedAtom);

  useEffect(() => {
    // Fetch summaries only once when component mounts
    const fetchSummaries = async () => {
      try {
        setIsLoading(true);
        const response = await fetch("/api/summary", {
          cache: "no-store",
        });

        if (!response.ok) {
          setError(true);
          return;
        }

        const summaries: Summary[] = await response.json();

        // Process summaries
        const processed = summaries.map((summary) => {
          // Try to extract a title from the summary content
          const title =
            summary.summary
              .split("\n")[0]
              .replace(/<[^>]*>/g, "")
              .substring(0, 50) + (summary.summary.length > 50 ? "..." : "");

          // Generate tags
          const tags = ["resumen", "ai"];

          return {
            id: summary.id,
            title: title,
            content: summary.summary,
            date: summary.createdAt,
            originalText: summary.text,
            tags: tags,
          };
        });

        setProcessedSummaries(processed);
      } catch (err) {
        setError(true);
        console.error("Error fetching summaries:", err);
      } finally {
        setIsLoading(false);
      }
    };

    fetchSummaries();
  }, []);

  const handleDeleteSummary = async (id: string) => {
    try {
      setSummaryToDelete(null); // Reset after deletion attempt
      const response = await fetch(`/api/summary?id=${id}`, {
        method: "DELETE",
      });

      if (!response.ok) {
        throw new Error("Failed to delete summary");
      }

      // Remove the deleted summary from the state
      setProcessedSummaries((prev) =>
        prev.filter((summary) => summary.id !== id)
      );

      toast.success("Resumen eliminado", {
        description: "El resumen ha sido eliminado correctamente.",
      });
    } catch (err) {
      console.error("Error deleting summary:", err);
      toast.error("Error", {
        description:
          "No se pudo eliminar el resumen. Intenta de nuevo más tarde.",
      });
    }
  };

  const handleSelectSummary = (summary: ProcessedSummary) => {
    setSelectedSummary(summary);
    setSummaryFromSaved(true);
  };

  if (isLoading) {
    return (
      <div className="text-center pb-10">
        <p className="text-gray-500 dark:text-gray-400">
          Cargando resúmenes...
        </p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center pb-10">
        <p className="text-gray-500 dark:text-gray-400">
          Error al cargar los resúmenes. Por favor, intenta de nuevo más tarde.
        </p>
      </div>
    );
  }

  if (processedSummaries.length === 0) {
    return (
      <div className="text-center pb-10">
        <p className="text-gray-500 dark:text-gray-400">
          No hay resúmenes guardados todavía.
        </p>
      </div>
    );
  }

  return (
    <ScrollArea className="w-full h-[500px] pr-4">
      <div className="space-y-4">
        {processedSummaries.map((summary) => (
          <Card
            key={summary.id}
            className="w-full cursor-pointer hover:bg-slate-50 dark:hover:bg-slate-800 transition-colors"
            onClick={() => handleSelectSummary(summary)}
          >
            <CardHeader>
              <CardDescription>
                {new Date(summary.date).toLocaleDateString("es-ES")}
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div
                className="line-clamp-3"
                dangerouslySetInnerHTML={{ __html: summary.content }}
              />
            </CardContent>
            <CardFooter className="flex justify-between items-center">
              <div className="flex gap-2">
                {summary.tags.map((tag) => (
                  <Badge key={tag} variant="secondary">
                    {tag}
                  </Badge>
                ))}
              </div>
              <div className="flex gap-2">
                <AlertDialog>
                  <AlertDialogTrigger asChild>
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => setSummaryToDelete(summary.id)}
                    >
                      Eliminar
                    </Button>
                  </AlertDialogTrigger>
                  <AlertDialogContent>
                    <AlertDialogHeader>
                      <AlertDialogTitle>¿Estás seguro?</AlertDialogTitle>
                      <AlertDialogDescription>
                        Esta acción no se puede deshacer. Esto eliminará
                        permanentemente el resumen de la base de datos.
                      </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                      <AlertDialogCancel
                        onClick={() => setSummaryToDelete(null)}
                      >
                        Cancelar
                      </AlertDialogCancel>
                      <AlertDialogAction
                        onClick={() =>
                          summaryToDelete &&
                          handleDeleteSummary(summaryToDelete)
                        }
                      >
                        Eliminar
                      </AlertDialogAction>
                    </AlertDialogFooter>
                  </AlertDialogContent>
                </AlertDialog>
              </div>
            </CardFooter>
          </Card>
        ))}
      </div>
    </ScrollArea>
  );
}
